.list {
  position: fixed;
  top: 48px;
  left: 0;
  bottom: 0;
  width: 14%;
  padding: 0 0 40px;
  overflow-y: auto;
  z-index: 1; }
  .list::-webkit-scrollbar {
    display: none; }

.box {
  position: relative;
  margin: 8px 0;
  padding-left: 16px;
  line-height: 32px;
  font-weight: 500;
  font-size: 14px;
  color: #7e8e99;
  transition: all 100ms ease-in-out;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }
  .box:hover {
    color: #000000;
    background-color: #fcc800; }
  .box[data-current="true"] {
    pointer-events: none;
    color: #000000;
    background-color: #fcc800; }
