.course {
  position: relative;
  margin: 8px 0 12px;
  padding: 8px;
  line-height: 1.25;
  background-color: #fff;
  border-radius: 8px;
  transition: all 250ms ease;
  cursor: pointer; }
  .course:hover {
    z-index: 1;
    transform: scale(1.4);
    box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.24); }

.header {
  display: flex;
  justify-content: space-between;
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 12px;
  color: #7e8e99;
  line-height: 20px; }

.time {
  font-weight: 500; }

.price[data-cheap="true"] {
  color: #fcc800;
  font-weight: 500; }

.title {
  font-weight: normal;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.info {
  position: relative;
  display: flex;
  align-items: center;
  margin-top: 2px;
  font-size: 12px;
  color: #7e8e99; }

.avatar {
  margin-right: 8px;
  min-width: 24px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  overflow: hidden; }

.info-main {
  flex: 1;
  transform: scale(0.8);
  transform-origin: left;
  overflow: hidden; }

.tags {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.status {
  position: absolute;
  right: 0;
  bottom: 2px;
  opacity: 0.7;
  font-weight: 500;
  transform: scale(0.8);
  transform-origin: right; }
  .status[data-status="0"], .status[data-status="3"] {
    display: none; }
  .status[data-status="1"] {
    color: orange; }
  .status[data-status="2"] {
    color: red; }
