.popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  z-index: 10; }

.mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3); }

.container {
  position: relative;
  width: 660px;
  max-height: 90vh;
  background-color: #fff;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.24); }
  .container .close {
    position: absolute;
    top: 0;
    right: -40px;
    width: 32px;
    height: 32px;
    background: no-repeat center/18px url("/static/close.svg");
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 50%;
    transition: background-color 200ms ease-in-out;
    cursor: pointer; }
    .container .close:hover {
      background-color: #fcc800; }
  .container iframe {
    display: block;
    width: 100%;
    height: 310px;
    background-color: #000;
    border-radius: 8px 8px 0 0;
    overflow: hidden; }

.content {
  flex: 1;
  padding: 12px 20px;
  overflow-y: auto;
  line-height: 1.8;
  user-select: text; }
  .content .title {
    display: inline-block;
    margin: 8px 0 8px -20px;
    padding: 0 12px 0 20px;
    color: #fff;
    font-weight: 500;
    background-color: #fcc800; }
  .content p {
    font-size: 12px;
    color: #555555; }
